<template>
  <div class="seletor">
    <Form label-position="top">
      <FormItem>
        <h2>印刷机名称：</h2>
        <Input v-model="searchInput" size="large" placeholder="请输入印刷机名称"></Input>
      </FormItem>
      <FormItem style="text-align: center;">
        <router-link :to="searchInput&&{name:'List',params:{search:searchInput}}">
          <Button type="primary" size="large" @click="deleteLocalStorage">查询</Button>
        </router-link>
        <!--<Button style="margin-left: 8px" size="large" @click="$router.push('/')">返回</Button>-->
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'Index',
    data() {
      return {
        searchInput: ''
      }
    },
    methods: {
      deleteLocalStorage: function () {
        if (this.searchInput === '') {
          this.$Message.error('请输入有效值！');
        }
        localStorage.removeItem('list');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .seletor {
    padding: 120px 24px 24px;
  }

  h2 {
    margin-bottom: 15px;
  }
</style>



