import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Search from '@/components/Search'
import List from '@/components/List'
import Detail from '@/components/Detail'
import qsSearch from '@/components/qsSearch'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Search',
      name: 'Search',
      component: Search
    }
    ,
    {
      path: '/List',
      name: 'List',
      component: List,
      params:{
        search:''
      }
    }
    ,
    {
      path: '/Detail/:Id',
      name: 'Detail',
      component: Detail
    }
    ,
    {
      path: '/qsSearch',
      name: 'qsSearch',
      component: qsSearch
    }
  ]
})



