<template>
  <div class="seletor">
    <Form label-position="top">
      <FormItem>
        <h2>印刷机名称：</h2>
        <Input v-model="searchInput" size="large" placeholder="输入问题" @input="search" :clearable="true"></Input>
      </FormItem>
    </Form>
    <div class="status text-center">
      {{status}}
    </div>
    <ol>
      <li v-for="item in result">
        <span class="extra">
          <router-link :to="{name:'Detail',params:{Id:item.id}}">
            <Button type="default">查看</Button>
          </router-link>
        </span>
        <span>{{item.name}}</span>
      </li>
    </ol>
  </div>
</template>

<script>
  import _ from 'lodash'
  import axios from 'axios'

  let sources = [];
  export default {
    name: 'Index',
    data() {
      return {
        searchInput: '', //查询条件
        result: '',       //查询结果
        status: ''
      }
    },
    methods: {
      search: _.debounce(function () {
        let that = this;
        //删除已经结束请求
        _.remove(sources, function (n) {
          //过滤条件重新创建数组
          return n.source === null;
        })
        //取消还未结束的请求
        sources.forEach(function (item) {
          if (item !== null && item.source !== null && item.status === 1) {
            item.status = 0;
            item.source.cancel('取消上一个');
          }
        });
        //创建新的请求cancelToken，并设置状态请求中
        var sc = {
          source: axios.CancelToken.source(),
          status: 1 //状态1：请求中，0：取消中
        };
        //这个对象加入数组中
        sources.push(sc);
        localStorage.getItem('list') && localStorage.removeItem('list');
        if (!localStorage.getItem('list')) {
          axios.get('/inkSupSysSize', {
            cancelToken: sc.source.token,
            params: {
              brand: this.searchInput,
              name: this.searchInput,
              size: this.searchInput
            }
          }).then(res => {
            //请求成功
            sc.source = null; //清空请求canceltoken
            if (this.result && res.data.data.content.length === 0) {
              this.status = '暂无结果'
            } else {
              this.status = ''
            }
            this.result = res.data.data.content;
            localStorage.setItem('list',JSON.stringify(this.result));
            localStorage.setItem('searchTxt',this.searchInput);
          }).catch(function (thrown) {
              //请求失败
              sc.source = null;

              if (axios.isCancel(thrown)) {
                console.log('请求取消')
              }
              else {

              }
            }
          )
        }
      }, 500)
    }
    ,mounted() {
      this.result = JSON.parse(localStorage.getItem('list'));
      this.searchInput = localStorage.getItem('searchTxt');

    }
  }
</script>

<style lang="scss" scoped>
  .seletor {
    padding: 15px 24px 24px;
  }

  h2 {
    margin-bottom: 15px;
  }

  .demo-spin-icon-load {
    animation: ani-demo-spin 1s linear infinite;
  }

  @keyframes ani-demo-spin {
    from {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  ol {
    padding-left: 15px;
  }

  li {
    line-height: 32px;
    height: 32px;
    font-size: 14px;
    margin-bottom: 10px;
  }

  .extra {
    float: right;
  }

  .text-center {
    text-align: center;
  }
</style>



