<template>
  <div id="app">

    <div class="layout">
      <Layout>
        <Header class="header">
          <div class="back" v-show="$route.name!='Index'" @click="$router.back(-1)">
            <Icon type="ios-arrow-back" />
          </div>
          <h1>供墨系统</h1>
        </Header>
        <Content class="content">
            <router-view/>
        </Content>
        <Footer class="footer">
          Copyright© 2015-现在 飓风网 JF61.com 版权所有
        </Footer>
      </Layout>
    </div>

  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      transitionName:''
    }
  }
  ,watch:{
    $route(to,from){
      if(to.meta.index > from.meta.index){
        this.transitionName='slide-left'
      }else{
        this.transitionName='slide-right'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .layout {
  h1 {
    color: #ffffff;
    text-align: center;
  }

  .header {
    background: #309af1;
  }

  .content{
    background: #fff;
    padding: 24px;
  }

  .footer {
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #f5f5f5;
    color: #333333;
    font-size: 12px;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .back{
    position: absolute;
    color: #ffffff;
    font-size: 25px;
  }

  }
  .slide-right-enter-active,
  .slide-right-leave-active,
  .slide-left-enter-active,
  .slide-left-leave-active {
    will-change: transform;
    transition: all 500ms;
    position: absolute;
  }
  .slide-right-enter {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  .slide-right-leave-active {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  .slide-left-enter {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }
  .slide-left-leave-active {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
</style>



