<template>
  <div>
    <div class="status text-center">
      {{status}}
    </div>
    <ol>
      <li v-for="item in list">
        <span class="extra">
          <router-link :to="{name:'Detail',params:{Id:item.id}}">
            <Button type="default">查看</Button>
          </router-link>
        </span>
        <span>{{item.name}}</span>
      </li>
    </ol>
    <!--<div class="text-center" style="margin-top:20px;">-->
      <!--<Button type="default" @click="$router.push('/Search')">重新搜索</Button>-->
    <!--</div>-->
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        switchValue: true,
        list:[],
        searchParams:'',
        status:''
      }
    },
    mounted(){
      if(!localStorage.getItem('list')){
        axios.get('/inkSupSysSize',{
          params: {
            brand:this.$route.params.search,
            name:this.$route.params.search,
            size:this.$route.params.search
          }
        }).then( res => {
          if(res.data.data.content.length === 0){
            this.status='暂无结果'
          }
          this.list = res.data.data.content;
          localStorage.setItem('list',JSON.stringify(this.list));
        })
      }
      else{
        this.list=JSON.parse(localStorage.getItem('list'));
      }
    }
  }
</script>

<style lang="scss" scoped>
  ol{
    padding-left:15px;
  }
  li{
    line-height: 32px;
    height: 32px;
    font-size: 14px;
    margin-bottom: 10px;
  }
  .extra{
    float: right;
  }
  .text-center{
    text-align: center;
  }
</style>



